@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
/*  styling scrollbars  */
::-webkit-scrollbar {
  width: 5px;
  height: 6px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #a5aaad;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #3ea175;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #a5aaad;
}

* {
  margin: 0;
  padding: 0;
}

body {
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

.text-primary-p {
  color: #a5aaad;
  font-size: 14px;
  font-weight: 700;
}

.font-bold {
  font-weight: 700;
}

.text-title {
  color: #2e4a66;
}

.text-lightblue {
  color: #469cac;
}

.text-red {
  color: #cc3d38;
}

.text-yellow {
  color: #a98921;
}

.text-green {
  color: #3b9668;
}

.container {
  display: grid;
  height: 100vh;
  grid-template-columns: 0.8fr 1fr 1fr 1fr;
  grid-template-rows: 0.2fr 3fr;
  grid-template-areas:
    "sidebar nav nav nav"
    "sidebar main main main";
}

@media only screen and (max-width: 978px) {
  .container {
    grid-template-columns: 1fr;
    grid-template-rows: 0.2fr 3fr;
    grid-template-areas:
      "nav"
      "main";
  }
}
main {
    background: #ebedef;
    grid-area: main;
    overflow-y: auto;
  }
  
  .main__container {
    padding: 20px 35px;
    margin-top: 60px;
  }
  
  .main__title {
    display: flex;
    align-items: center;
  }
  
  .main__title > img {
    max-height: 100px;
    object-fit: contain;
    margin-right: 20px;
  }
  
  .main__greeting > h1 {
    font-size: 24px;
    color: #2e4a66;
    margin-bottom: 5px;
  }
  
  .main__greeting > p {
    font-size: 14px;
    font-weight: 700;
    color: #a5aaad;
  }
  
  .main__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 30px;
    gap: 30px;
    margin: 20px 0;
  }
  
  .card1 {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 130px;
    padding: 25px;
    border-radius: 5px;
    background: #33ccff;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #ffffff;
  }
  .card2 {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 130px;
    padding: 25px;
    border-radius: 5px;
    background: #4799eb;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #ffffff;
  }
  .card3 {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 130px;
    padding: 25px;
    border-radius: 5px;
    background: #ba7dec;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #ffffff;
  }
  .card4 {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 130px;
    padding: 25px;
    border-radius: 5px;
    background: #ff99cc;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #ffffff;
  }
  
  .card_inner1 {
    display: flex;
    flex-direction: column;
    min-width: 0;
    background-clip: border-box;
    border-radius: .25rem;
    background-color: #33ccff;
  }
  .card_inner2 {
    display: flex;
    flex-direction: column;
    min-width: 0;
    background-clip: border-box;
    border-radius: .25rem;
    background-color: #4799eb;
  }
  .card_inner3 {
    display: flex;
    flex-direction: column;
    min-width: 0;
    background-clip: border-box;
    border-radius: .25rem;
    background-color: #ba7dec;
  }
  .card_inner4 {
    display: flex;
    flex-direction: column;
    min-width: 0;
    background-clip: border-box;
    border-radius: .25rem;
    background-color: #ff99cc;
  }
  .text_white {
    color: #fff;
  }
  .card_inner > span {
    font-size: 25px;
  }
  
  .charts {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
    gap: 30px;
    margin-top: 50px;
  }
  
  
  .charts__left {
    padding: 25px;
    border-radius: 5px;
    background: #ffffff;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #ffffff;
  }
  
  .charts__left__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .charts__left__title > div > h1 {
    font-size: 24px;
    color: #2e4a66;
    margin-bottom: 5px;
  }
  
  .charts__left__title > div > p {
    font-size: 14px;
    font-weight: 700;
    color: #a5aaad;
  }
  
  .charts__left__title > i {
    color: #ffffff;
    font-size: 20px;
    background: #33ccff;
    border-radius: 200px 0px 200px 200px;
    -moz-border-radius: 200px 0px 200px 200px;
    -webkit-border-radius: 200px 0px 200px 200px;
    border: 0px solid #000000;
    padding: 15px;
  }
  
  .charts__right {
    padding: 25px;
    border-radius: 5px;
    background: #ffffff;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #ffffff;
  }
  
  .charts__right__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .charts__right__title > div > h1 {
    font-size: 24px;
    color: #2e4a66;
    margin-bottom: 5px;
  }
  
  .charts__right__title > div > p {
    font-size: 14px;
    font-weight: 700;
    color: #a5aaad;
  }
  
  .charts__right__title > i {
    color: #ffffff;
    font-size: 20px;
    background: #ff99cc;
    border-radius: 200px 0px 200px 200px;
    -moz-border-radius: 200px 0px 200px 200px;
    -webkit-border-radius: 200px 0px 200px 200px;
    border: 0px solid #000000;
    padding: 15px;
  }
  
  .charts__right__cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    gap: 20px;
    margin-top: 30px;
  }
  
  @media only screen and (max-width: 855px) {
    .main__cards {
      grid-template-columns: 1fr;
      grid-gap: 10px;
      gap: 10px;
      margin-bottom: 0;
    }
  
    .charts {
      grid-template-columns: 1fr;
      margin-top: 30px;
    }
  }
.App {
    font-family: sans-serif;
    text-align: center;
  }
  
.navbar {
  background: -webkit-linear-gradient(left,#33ccff 0%, #ff99cc 100%);
  grid-area: nav;
  height: 60px;
  width: 1550px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30px 0 25px;
  z-index: 100000;
  position: fixed;
  border-bottom: 1px solid lightgray;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}
  .sidebar__img {
    margin-left: 40px;
    margin-top: 30px;
    display: flex;
    color: #fff;
    margin-bottom: 30px;
    align-items: center;
  }
.text{
  margin-left: 10px;
  color: #fff;
  align-items: center;
}
.navbar__left > a {
  margin-right: 30px;
  text-decoration: none;
  color: #ffffff;
  font-size: 35px;
  align-items: center;
  justify-content: center;
  font-weight: 700;
}

.navbar__right {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar__right > a {
  margin-left: 20px;
  text-decoration: none;
}

.navbar__right > a > i {
  color: #a5aaad;
  font-size: 16px;
  border-radius: 50px;
  background: #ffffff;
  box-shadow: 2px 2px 5px #d9d9d9, -2px -2px 5px #ffffff;
  padding: 7px;
}
.nav-menu {
  background-color: #2399c0;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  margin-top: 60px;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1457a8;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #33ccff;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.developers,
.users,
.games,
.setting,
.support,
.notification,
.info,
.logout{
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
